import react from 'react'
import { Collapse, CollapseItem, Toast } from '@antmjs/vantui'

export default function Demo() {
  const [values, setValues] = react.useState(['1'])

  return (
    <>
      <Collapse
        value={values}
        onChange={(e) => setValues(e.detail)}
        onOpen={(e) => Toast.show(`${e.detail}`)}
        onClose={(e) => Toast.show(`${e.detail}`)}
      >
        <CollapseItem title="" name="1">
          
        </CollapseItem>
        <CollapseItem title="" name="2">
          
        </CollapseItem>
        <CollapseItem title="" name="3">
          
        </CollapseItem>
      </Collapse>
      <Toast id="collapse-mess" />
    </>
  )
}
