/* eslint-disable */
import react from 'react'
import { Collapse, CollapseItem } from '@antmjs/vantui'

export default function Demo() {
  const [values, setValues] = react.useState(['1'])
  const [dynamic, setdynamic] = react.useState('')

  react.useEffect(() => {
    setTimeout(() => {
      setdynamic('')
    }, 1000)
  }, [])

  return (
    <Collapse value={values} onChange={(e) => setValues(e.detail)}>
      <CollapseItem title="" name="1">
        
        {dynamic}
      </CollapseItem>
      <CollapseItem title="" name="2">
        
        {dynamic}
      </CollapseItem>
      <CollapseItem title="" name="3" disabled>
        
        {dynamic}
      </CollapseItem>
    </Collapse>
  )
}
